.ContentBlockProject{
    padding: 30px; 
    float: left; 
    width: 70%; 
    font-weight: 500;
    font-size: 20px;
    margin-right: 10px;
    background-color: #1d1d1d;
    border-radius: 10px;
}
.aside-block{
    float: right; 
    width: 200px; 
}
button{
    display: flex;
    flex-flow: column wrap;;
    text-decoration: none;
    padding: 10px;
    border: none;
    font-size: 14px;
    font-family: inherit;
    color: #fff;
    width: 10em;
    height: 3em;
    border-bottom: 1px solid white ;
    line-height: 2em;
    text-align: center;
    background-color: #1a1818;
    background-size: 300%;
    cursor: pointer;
    z-index: 1;
}
button:hover {
    background-color: #272727;
    border: none;
}
button:active {
    transform: translateY(-1px);
}