
.MainContent{
    font-weight: 600;
    font-size: 20px;
    padding: 0 0 32px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -moz-box-orient: vertical;
    -moz-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    overflow: hidden;
}
.MainBox{
    background: radial-gradient(circle, #5d1a78, #414b50);
    padding: 40px;
    margin-bottom: 200px;
    border-radius: 100px;
}
.ContentBox{
    background-color: #1d1d1d;
    padding: 30px;
    margin-bottom: 80px;
    border-radius: 40px;
}
.HistoryBlock{
    font-weight: 500;
    font-size: 20px;
}
.BlockName{
    padding: 10px;
    text-align: center;
    border: 1px solid white;
    border-radius: 30px;
    font-weight: 600;
    font-size: 20px;
    color: #c94449;
    margin-bottom: 15px;
}
.ImgProject{
    width: 200px;
    padding-right: 50px;
}
.ImgProject:hover{
    transform: scale(1.2);
    transition: 0.5s;
}
.ProjectBlock{
    margin-top: 30px;
}
.socialNetworkStr{
    margin: 20px 0 10px 0;
}
.textSocial{
    padding: 15px;
    margin-left: 40px;
    font-size: 20px;
    text-decoration: none;
    font-weight: 600;
}
.icon-Social{
    float: left;
}
.socialNetworkStr:hover{
    transform: scale(1.2);
    transition: 0.5s;
}