@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
    margin: 0;
    padding:0;
}
body{
    background-color: #333;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}
a:-webkit-any-link {
    text-decoration: none;
    color: white;
    cursor: pointer;
}
::-webkit-scrollbar {
    width: 10px; 
}     
::-webkit-scrollbar-thumb {
    background: rgb(114, 110, 110); 
    border-radius: 20px; 
}
::-webkit-scrollbar-track {
    background:rgb(37, 34, 34)  ;
    box-shadow: 0 0 2px rgba(0, 0, 0, .2) inset; 
}


.container {
    padding-right: 20%;
    padding-left: 20%;
    margin-right: auto;
    margin-left: auto;
    float: left;
}
.MainContentWrapper{
    margin-top:200px;
}