.title{
    border-bottom: 1px solid white;
    padding: 10px 0 10px 0;
    font-size:30px;
    font-weight: 600;
}
.footer{
    border-top: 1px solid white;
    padding: 20px 0 10px 0;
}
.text-box{
    padding: 20px 0 20px 0;
}

.footer:after {
	content: " ";
	display: table;
	clear: both;
}
.button {
    float: right;
    line-height: 3;
    position: relative;
    overflow: hidden;
    height: 3rem;
    padding: 0 2rem;
    border-radius: 1.5rem;
    background: #3d3a4e;
    background-size: 400%;
    color: #fff;
  }
  
.button:hover::before {
  transform: scaleX(1);
}

.button-content {
  position: relative;
  z-index: 1;
}

.button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  transform: scaleX(0);
  transform-origin: 0 50%;
  width: 100%;
  height: inherit;
  border-radius: inherit;
  background: linear-gradient(
    82.3deg,
    rgba(150, 93, 233, 1) 10.8%,
    rgba(99, 88, 238, 1) 94.3%
  );
  transition: all 0.475s;
}

