header{
    width: 100%;
    position: fixed;
    background-color: #222;
    padding: 10px;
    font-weight: 600;
}
.buttons-header{
    padding: 7px 0 7px 0;
    margin-left: 80px;
}
.headButt{
    float: left;
    padding-left: 10px;
    color: white;
    font-size: 11pt;
}
.headButt:hover{
    color: rgb(79, 116, 148);
    transition: 0.2s;
}
main{
    margin: 50px 100px;
}
.logo{
    width: 30px;
    float: left;
}
.listTopNav{
    display: none;
}
.menu{
    position: fixed;
    width: 300px;
    z-index: 9997;
    top: 50px;
    right: auto;
    bottom: 0;
    left: 50%;
}
.MenuList{
    display: flex;
    flex-flow: column wrap;;
    text-decoration: none;
    padding: 10px;
    border: none;
    font-size: 14px;
    font-family: inherit;
    color: #fff;
    width: 10em;
    border-bottom: 1px solid white ;
    line-height: 2em;
    text-align: center;
    background-color: #1a1818;
    background-size: 300%;
    cursor: pointer;
    z-index: 1;
}
